.MovieCard {
  -ms-flex-preferred-size: 30%;
      flex-basis: 30%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  padding: 16px 8px;
  color: #424242;
  position: relative;
  -webkit-box-shadow: 0 0 2px 2px rgba(33, 33, 33, 0.5);
          box-shadow: 0 0 2px 2px rgba(33, 33, 33, 0.5);
  border-radius: 2px;
  margin-bottom: 2%;
  margin-right: 1%;
}

.MovieCard__Title {
  font-size: 24px;
}

.MovieCard__btn {
  border-radius: 2px;
  padding: 4px 8px;
  color: #fff;
  -webkit-box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 2px 2px 0 rgba(33, 33, 33, 0.5);
  position: absolute;
  top: 8px;
  right: 8px;
  cursor: pointer;
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
}

.MovieCard__btn:hover {
  -webkit-box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
          box-shadow: 0 4px 2px 0 rgba(33, 33, 33, 0.5);
  background-color: #3f51b5;
}

.MovieCard__btn_del {
  font-size: 14px;
  background: #ff5722;
}

.MovieCard__Description {
  font-size: 14px;
  padding: 8px 0 0 0;
}

.MovieCard__Raiting {
  font-size: 16px;
  padding: 24px 0 24px 0;
}

.MovieCard__chip-list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.MovieCard__Category {
  display: -ms-flexbox;
  display: flex;
  font-size: 14px;
  color: #fff;
  border-radius: 2px;
  background: #3f51b5;
  padding: 4px 8px;
  margin-right: 4px;
}



